import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/dist/query/react";
import { IMovies } from "../models/IMovies";
import {IMovieDetails} from "../models/IMovieDetails";

const API_KEY = "7e9c3cc2569e2d65abfce04276b956fb";
const language = "en-US";

export const moviesAPI = createApi({
  reducerPath: "moviesAPI",
  baseQuery: fetchBaseQuery({ baseUrl: "https://api.themoviedb.org/3/movie" }),
  endpoints: (build) => ({
    fetchPopularMovies: build.query<IMovies, number>({
      query: (page: number) => ({
        url: "/popular",
        params: {
          api_key: API_KEY,
          language: language,
          page: page,
        },
      }),
    }),
    fetchTopRatedMovies: build.query<IMovies, number>({
      query: (page: number) => ({
        url: "/top_rated",
        params: {
          api_key: API_KEY,
          language: language,
          page: page,
        },
      }),
    }),
    fetchNowPlayingMovies: build.query<IMovies, number>({
      query: (page: number) => ({
        url: "/now_playing",
        params: {
          api_key: API_KEY,
          language: language,
          page: page,

        },
      }),
    }),
    fetchUpcomingMovies: build.query<IMovies, number>({
      query: (page: number) => ({
        url: "/upcoming",
        params: {
          api_key: API_KEY,
          language: language,
          page: page,
        },
      }),
    }),
    fetchDetails: build.query<IMovieDetails, string | undefined>({
      query: (movie_id: string) => ({
        url: `${movie_id}`,
        params: {
          api_key: API_KEY,
          language: language,
        },
      }),
    }),
    fetchMoviesByGenre: build.query<IMovies, string | undefined>({
      query: (genre_id: string) => ({
        url: `${genre_id}`,
        params: {
          api_key: API_KEY,
          language: language,
        },
      }),
    }),
  }),
});
