import styled from 'styled-components';

export const PaginationStyled = styled.div`
  text-align: center;
  margin-bottom: 10px;
`;

export const PageButtonStyled = styled.span`
  background-color: #704cb6;
  color: white;
  height: 40px;
  width:45px;
  text-align: center;
  display: inline-block;
  font-size: 15px;
  line-height: 40px;
  margin: 4px;
  &:hover {
    background-color: #482787;
    cursor: pointer;
  }
`;