import { moviesAPI } from "../../services/MoviesService";
import { Header } from "../../components/Header/Header";
import { MovieCard } from "../../components/MovieCard/MovieCard";
import React, {useEffect, useState} from 'react';
import {Pagination} from '../../components/Pagination/Pagination';

export const UpcomingMovies = () => {

  const [page, setPage] = useState(parseInt(sessionStorage.getItem('upcoming_page') as string));

  sessionStorage.setItem('upcoming_page', isNaN(page) ? '1' : page.toString());

  useEffect(() => {
    sessionStorage.setItem('upcoming_page', page.toString());
  }, [page]);

  const changePage = (chosen_page: number) => setPage(chosen_page);

  const {
    data,
    error,
    isLoading,
  } = moviesAPI.useFetchUpcomingMoviesQuery(page);
  return (
    <>
      <Header />
      <h1>Upcoming</h1>
      <div className="list">
        {isLoading && <h1>Идет загрузка</h1>}
        {error && <h1>Ошибка</h1>}
        {data &&
        data.results.map((movie) => (
          <MovieCard key={movie.id} movie={movie}/>
        ))}
      </div>
      {data && <Pagination
        changePage={changePage}
        page={page}
        total_pages={data.total_pages}
      />}
    </>
  );
};
