import React from 'react';
import './App.css';
import { PopularMovies } from './pages/PopularMovies/PopularMovies';
import { TopRatedMovies } from './pages/TopRatedMovies/TopRatedMovies';
import { NowPlayingMovies } from './pages/NowPlayingMovies/NowPlayingMovies';
import { UpcomingMovies } from './pages/UpcomingMovies/UpcomingMovies';
import { Route, Routes } from 'react-router-dom';
import {MoviePage} from './pages/MoviePage/MoviePage';
import {MoviesByGenrePage} from './pages/MoviesByGenrePage/MoviesByGenrePage';

export function App() {
  return (
    <div className="App">
      <Routes>
        <Route path={'/'} element={<NowPlayingMovies />} />
        <Route path={'/Popular'} element={<PopularMovies />} />
        <Route path={'/Top%20rated'} element={<TopRatedMovies />} />
        <Route path={'/Upcoming'} element={<UpcomingMovies />} />
        <Route path={'/movies/:movie_id'} element={<MoviePage/>} />
        <Route path={'/:genre_id'} element={<MoviesByGenrePage/>} />
      </Routes>
    </div>
  );
}
